<template>
  <div class="dtg">
    <ul>
      <li v-for="(seq,index) in dataList">
        <dl class="tyv">  {{index + 1}} #1_1 {{seq.category}}</dl>
        <div class="fxdc">
          <div v-for="(task, i) in seq.taskList">
            <table border="1" class="aa3">
              <tbody>
              <tr>
                <td width="88" height="40" bgcolor="#f5f5f5"></td>
                <td width="118" bgcolor="#f5f5f5"></td>
                <td width="118" bgcolor="#f5f5f5"></td>
                <td width="118" bgcolor="#f5f5f5"></td>
                <td width="88" bgcolor="#f5f5f5"></td>
                <td width="376" bgcolor="#f5f5f5"></td>
              </tr>
              <tr>
                <td rowspan="5">{{task.version}}</td>
                <td rowspan="5">{{task.title}}</td>
                <td>
                  <ul>
                    <li v-for="material in task.listQuotaMaterialVersion">
                      {{material.title}}
                    </li>
                  </ul>
                </td>
                <td>
                  <ul>
                    <li v-for="machine in task.listQuotaMachineVersion">
                      {{machine.title}}
                    </li>
                  </ul>
                </td>
                <td>
                  <ul>
                    <li v-for="worker in task.listQuotaWorkerVersion">
                      {{worker.title}}
                    </li>
                  </ul>
                </td>
                <td rowspan="5">
                  <div class="erf">
                    <dl class="tyh">
                      <template v-for="(p, index) in task.fitStandard.processFlow">
                        {{index + 1}}: {{p.title}}
                        <template v-if="index != task.fitStandard.processFlow.length -1">
                          &gt;
                        </template>
                      </template>
                    </dl>
                    <dl class="tgbhn">
                      <router-link tag="input" value="" type="button" class="ytr" :to="{name:'fitStandardInfo',params: {fitStandardId: task.fitStandardId}}"></router-link>
                    </dl>
                  </div>
                </td>
              </tr>
              </tbody>
            </table>
            <dl class="etrf">
              <input type="button" class="anniu33" value="" @click="edit(task.id, task.fitStandardId)">
              <input type="button" class="anniu34" value="">
              <input type="button" class="umk12" value="" @click="addFitStandard(task.id)">
            </dl>
          </div>


          <dl class="ertvc">
            <input type="button" class="ybf" value="" @click="addFitTask(version, seq.taskList[0].phase)">
          </dl>
        </div>
      </li>
    </ul>
  </div>
</template>
<script>
  import axios from 'axios'
  export default {
    data() {
      return {
        phase: '',
        dataList: '',
        version: ''
      }
    },
    mounted: function () {
      this.phase = this.$route.query.phase
      this.version = this.$route.query.version
      this.getTaskList();
    },
    watch: {
      $route: function () {
        this.phase = this.$route.query.phase
        this.version = this.$route.query.version
        this.getTaskList()
      }
    },
    methods: {
      getTaskList() {
        var _this = this;
        var url = basePath + "/fit_task/version/"+this.version+"/phase/"+this.phase;
        axios.get(url).then(function (response) {
          _this.dataList = response.data;
          for (var i = 0; i < _this.dataList.length; i++) {
            for (var j = 0; j < _this.dataList[i].taskList.length; j++) {
              _this.dataList[i].taskList[j].fitStandard.processFlow = JSON.parse(_this.dataList[i].taskList[j].fitStandard.processFlow);
            }
          }
        });
      }, edit(fitTaskId, fitStandardId) {
        this.$router.push({path: '/edit_fit_task', query: {fitTaskId: fitTaskId, fitStandardId: fitStandardId}});
      }, addFitStandard(fitTaskId) {
        this.$router.push({path: '/add_fit_standard', query: {fitTaskId: fitTaskId}});
      }, addFitTask(version, phase, actualPhase) {
        this.$router.push({path: '/add_fit_task', query: {version: version, phase: phase}});
      }
    }
  }
</script>



