import Vue from 'vue'
import App from './App'
import router from './router'
import VueClip from 'vue-clip'
import Paginate from 'vuejs-paginate'
import VeeValidate, { Validator } from 'vee-validate'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-default/index.css'
Vue.component('paginate', Paginate);
Vue.use(VueClip);
Vue.use(ElementUI)
const dictionary = {
  zh_CN: {
    messages:{
      after: (field, [target]) => `${target}`,
      alpha_dash: (field) => ``,
      alpha_num: (field) => `.`,
      alpha_spaces: (field) => `.`,
      alpha: (field) => `  .`,
      before: (field, [target]) => `${target}.`,
      between: (field, [min, max]) => ` ${min} ${max}.`,
      confirmed: (field, [confirmedField]) => `${confirmedField}.`,
      date_between: (field, [min, max]) => `${min}${max}.`,
      date_format: (field, [format]) => `${format}.`,
      decimal: (field, [decimals] = ['*']) => ` ${decimals === '*' ? '' : decimals} .`,
      digits: (field, [length]) => ` ${length}`,
      dimensions: (field, [width, height]) => ` ${width}  ${height} .`,
      email: (field) => `.`,
      ext: (field) => `.`,
      image: (field) => `.`,
      in: (field) => `.`,
      ip: (field) => `.`,
      max: (field, [length]) => `${length}.`,
      mimes: (field) => `.`,
      min: (field, [length]) => ` ${length} .`,
      not_in: (field) => `.`,
      numeric: (field) => `.`,
      regex: (field) => `.`,
      required: (field) => `.`,
      size: (field, [size]) => ` ${size} KB.`,
      url: (field) => `url.`

    }
  }
};
Validator.updateDictionary(dictionary);
Vue.use(VeeValidate, {locale: "zh_CN"});


Vue.config.productionTip = false
new Vue({
  el: '#app',
  router,
  template: '<App/>',
  components: {App}
})



